.hero {
  background: linear-gradient(-65.1deg,
  $c-athens-gray 22%,
  $c-picton-blue 0);
  @media (max-width: 1450px) {
    & {
      background: linear-gradient(-65.1deg,
      $c-athens-gray 20%,
      $c-picton-blue 0);
    }
  }

  @media (max-width: 1350px) {
    & {
      background: linear-gradient(-65.1deg,
      $c-athens-gray 18%,
      $c-picton-blue 0);
    }
  }

  @media (max-width: 1250px) {
    & {
      background: linear-gradient(-65.1deg,
      $c-athens-gray 15%,
      $c-picton-blue 0);
    }
  }
}

.header {
  position: relative;
  height: 848px;
  max-width: 1146px;
  margin: 0 auto;

  &__top {
    padding: 48px 0 80px;
    max-width: 948px;
    display: flex;
    align-items: center;
  }

  @media (max-width: 1450px) {
    & {
      height: 748px;
    }
  }

  &__content {
    margin-left: 98px;
    max-width: 754px;
    h1 {
      margin-bottom: 22px;
    }

    p {
      font-weight: normal;
      font-size: 24px;
      line-height: 150%;
      color: $c-white;
      margin-bottom: 42px;
    }
  }

  &__intro {
    position: absolute;
    background-image: linear-gradient(0deg,
    rgba(37, 55, 87, 0.1),
    rgba(37, 55, 87, 0.1)),
      url("../images/intro.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    height: 640px;
    bottom: -320px;
    left: 0;
    right: 0;

    @media (max-width: 1450px) {
      & {
        left: 95px;
        right: 55px;
        height: 480px;
        bottom: -240px;
      }
    }
  }
}

.logo {
  margin-right: auto;
  flex-basis: 135px;
  &__link {
    font-weight: bold;
    font-size: 32px;
    line-height: 100%;
    color: $c-white;
  }
}

.hire {
  flex-basis: 135px;
  text-align: end;
  margin-left: auto;
  margin-right: 5px;
  color: $c-white;
}

.hire,
.nav__item {
  font-weight: bold;
  font-size: 16px;
  line-height: 150%;
}

.slides {
  position: absolute;
  top: 287px;
  left: -81px;
  display: flex;
  width: 162px;
  height: 64px;
  background-color: $c-white;
  border-radius: 50px;

  @media (max-width: 1450px) {
    & {
      top: 207px;
    }
  }

  &__prev {
    &-link {
      border-top-left-radius: 50px;
      border-bottom-left-radius: 50px;
      &::after {
        border-left: 2px solid $c-white;
        border-bottom: 2px solid $c-white;
      }
    }
  }

  &__next {
    &-link {
      border-top-right-radius: 50px;
      border-bottom-right-radius: 50px;
      &::after {
        border-top: 2px solid $c-white;
        border-right: 2px solid $c-white;
      }
    }
  }

  &__prev,
  &__next {
    &-link {
      height: 64px;
      width: 81px;
      display: flex;
      justify-content: center;
      align-items: center;
      background: linear-gradient(180deg,
      rgba(255, 255, 255, 0.25) 0%,
      rgba(255, 255, 255, 0) 100%),
        $c-amber;
      transition: 0.3s;
    }

    &-link:hover {
      opacity: 0.8;
    }

    &-link::after {
      content: "";
      display: inline-block;
      transform: rotate(45deg);
      transform-origin: center center;
      width: 18px;
      height: 18px;
      transition: 0.3s;
    }

    &-link:hover::after {
      border-color: $c-azure;
      border-width: 3px;
    }
  }
}

.note-block {
  width: 264px;
  position: absolute;
  bottom: 86px;
  left: 98px;
  &__name {
    font-weight: 800;
    font-size: 12.6px;
    line-height: 150%;
    color: $c-picton-blue;
    text-transform: uppercase;
    letter-spacing: 2px;
    padding-bottom: 4px;
  }

  &__text {
    font-weight: 900;
    font-size: 19.6px;
    line-height: 140%;
    color: $c-white;
  }
}

.socials-links {
  position: absolute;
  right: -395px;
  bottom: 200px;
  width: 640px;
  display: flex;
  transform: rotate(90deg);
  transform-origin: center center;

  @media (max-width: 1450px) {
    & {
      bottom: 120px;
      right: -355px;
    }
  }

  &__item {
    font-weight: 800;
    font-size: 12.6px;
    line-height: 150%;
    letter-spacing: 2px;
    text-transform: uppercase;
    color: $c-cloud-burst;

    &:not(:last-child) {
      margin-right: 45px;
    }
    & a {
      display: inline-block;
      padding: 5px 15px;
      border-radius: 20px;
      transition: 0.3s;
    }
    & a:hover {
      background-color: $c-amber;
      color: $c-azure;
      opacity: 0.8;
    }
  }
}
